import React from "react";
import { Breadcrumb, Space, Row, Col, Card } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";

export default function Home() {
  return (
    <>
      <Space direction="vertical" style={{ display: "flex" }}>
        <Breadcrumb
          items={[
            {
              href: "/home",
              title: <HomeOutlined />,
            },
            {
              href: "/home",
              title: "首页",
            },
          ]}
          style={{ marginBottom: "8px" }}
        />
        <div style={{ margin: "8px 0" }}>欢迎使用 Quests，想做些什么？</div>
        <Row>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="浏览题目" extra={<Link to="/quests/question">出发！</Link>}>
              <p>你可以在这里浏览题目，并使用名称或标签搜索题目。</p>
            </Card>
          </Col>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="浏览标签" extra={<Link to="/quests/tag">出发！</Link>}>
              <p>你可以给题目设置不同的标签，并在这里浏览各个标签。</p>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="设置与同步" extra={<Link to="/quests/setting">出发！</Link>}>
              <p>
                你可以在这里进行一些设置，保存题目信息为 JSON
                并存储在电脑上。同时你也可以将电脑上的 JSON 同步到 Quests。
              </p>
            </Card>
          </Col>
          <Col span={11} style={{ margin: "8px" }}>
            <Card
              title="查看题组"
              extra={<Link to="/quests/questionSet">出发！</Link>}
            >
              <p>你可以创建题组以更好地管理题目，并在这里查看和管理它们。</p>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="关于" extra={<Link to="/quests/about">出发！</Link>}>
              <p>有关 Quests 项目的一些信息。</p>
            </Card>
          </Col>
        </Row>
      </Space>
    </>
  );
}
